html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

html {
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
}

.main > .content {
  padding: 0 15%;
}

.header {
  background: #000 url(./../img/task/header.png) 0 0/100% 100% no-repeat;
  max-height: 850px;
}

.navigation {
  display: flex;
  height: 110px;
}

.logo {
  display: flex;
  align-items: center;
  color: #fff;
  cursor: pointer;
}

.logo_close, .logo_img_small {
  display: none;
}

.logo_img {
  margin-bottom: 15px;
}

.nav {
  display: flex;
  margin: 0 0 0 auto;
  justify-content: center;
}

.nav_item {
  display: flex;
  align-items: center;
  font-size: 20px;
}

.nav_item + .nav_item {
  margin-left: 40px;
}

.nav_item a {
  color: #fff;
  text-decoration: none;
  text-align: center;
}

.main_page {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 50px;
}

.mobile_main {
  margin-bottom: 50px;
}

.article_main {
  max-width: 60%;
  color: #fff;
}

.title_main {
  font-size: 38px;
}

.text_main {
  font-size: 20px;
}

.btn_main {
  margin-top: 50px;
  border: 1px solid #fff;
  border-radius: 3px;
  background: transparent;
  text-transform: uppercase;
  color: #fff;
  padding: 15px 50px;
  font-size: 15px;
  cursor: pointer;
  outline: none;
}

.advantages {
  background-color: #fff;
}

.article_advantages {
  padding: 100px 0;
}

.article_one {
  font-size: 18px;
  text-align: center;
  color: #ff8400;
  text-transform: uppercase;
}

.article_two {
  font-size: 36px;
  font-size-text-align: center;
}

.description {
  width: 25%;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 0 4px;
}

.description_advantages {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding-bottom: 100px;
  justify-content: space-between;
  margin: 0;
}

.bot {
  background: #000 url(./../img/task/bg2.png) 0 0/100% 100% no-repeat;
  max-height: 567px;
  text-align: center;
  padding-top: 100px;
}

.bot_container {
  width: 100%;
  padding-top: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title_bot {
  font-size: 36px;
  margin: 0 0 35px 0;
  text-align: center;
  color: #fff;
}

.article_bot {
  color: #fff;
  max-width: 60%;
  margin-bottom: 50px;
}

.form_bot {
  margin-bottom: 100px;
  width: 100%;
}

.input_bot {
  width: 40%;
  box-sizing: border-box;
  border: 1px solid #fff;
  border-radius: 3px;
  background: transparent;
  padding: 15px 50px;
  font-size: 15px;
  outline: none;
  color: #fff;
}

.input_bot::placeholder {
  color: #fff;
}

.but_bot {
  height: 100%;
  margin-left: 30px;
  border: 1px solid #fff;
  border-radius: 3px;
  background: #fff;
  text-transform: uppercase;
  color: #ff8400;
  padding: 15px 50px;
  font-size: 15px;
  cursor: pointer;
  outline: none;
}

.footer {
  height: 110px;
  background-color: #fff;
  display: flex;
  align-items: center;
}

.footer__buttons {
  border: 0;
  background: none;
  cursor: pointer;
}

.footer__buttons + .footer__buttons {
  margin-left: 5px;
}

.copyright_footer {
  margin: 0;
  padding: 50px 0;
  font-size: 10px;
}

.social_icons {
  margin-left: auto;
}

.text_description, .title_description, .article_bot {
  font-size: 16px;
}

@media (min-width: 320px) and (max-width: 1023px) {
  .header {
    max-height: none;
  }
  .bot {
    max-height: none;
  }
  .description {
    width: 100%;
  }
  .article_bot {
    padding: 0;
  }
  .input_bot {
    width: auto;
  }
  .form_bot {
    display: flex;
    width: auto;
  }
  .main_page {
    flex-direction: column;
  }
  .article_main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 35px;
  }
  .main > .content {
    padding: 0 5%;
  }
}

@media screen and (min-width: 320px) and (max-width: 749px) {
  .nav, .logo_img {
    display: none;
  }
  .logo_img_small {
    display: block;
    width: 50px;
  }
  .form_bot {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .input_bot {
    width: auto;
  }
  .but_bot {
    margin: 30px 0 0;
  }
  #logo:checked + .nav {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.9);
    z-index: 1;
    padding-left: 5%;
  }
  #logo:checked + .nav .nav_item {
    margin-left: 0;
  }
  .logo_close {
    display: block;
    color: #fff;
    cursor: pointer;
    font-size: 20px;
    margin-bottom: 20px;
  }
}
